import i18n from "../i18n";

const languageAliasMap = {
  // 
  "zh-TW": "zhhant",
  "zh-HK": "zhhant",
  "zh-MO": "zhhant",
  "zh-Hant": "zhhant",

  // 
  "zh-CN": "zhhans",
  "zh-SG": "zhhans",
  "zh-Hans": "zhhans",

  // 
  en: "en",
  "en-US": "en",
  "en-GB": "en",
  "en-AU": "en",
  "en-CA": "en",
  "en-NZ": "en",
  "en-IN": "en",
  "en-SG": "en",

  // 
  ja: "ja",
  "ja-JP": "ja",

  // 
  ko: "ko",
  "ko-KR": "ko",

  // 
  es: "es",
  "es-ES": "es",
  "es-MX": "es",
  "es-AR": "es",
  "es-CL": "es",
  "es-CO": "es",
  "es-PE": "es",

  // 
  fr: "fr",
  "fr-FR": "fr",
  "fr-BE": "fr",
  "fr-CA": "fr",
  "fr-CH": "fr",

  // 
  de: "de",
  "de-DE": "de",
  "de-AT": "de",
  "de-CH": "de",

  // 
  hi: "hi",
  "hi-IN": "hi",
};

export default function detectAndApplyLanguageAlias() {
  const detectedLng = i18n.language;
  const baseLng = detectedLng.split("-")[0];
  const alias = languageAliasMap[detectedLng] || languageAliasMap[baseLng];

  if (alias && alias !== i18n.language) {
    i18n.changeLanguage(alias);
  }
}
